@media only screen and (min-width: 640px) {
    .memory_board {
        max-width: 386px;
    }
}

.memory_board {
    background-color: rgb(240,240,240);
    border: 1px solid rgb(200,200,200);
    border-radius: 5px;
    box-shadow: 10px 10px 40px rgba(20,20,20,0.3);
    display: flex;
    flex-flow: row wrap;
    margin: 5px;
    padding: 5px;
    width: 386px;
}
